'use client';
import { useEffect, useState } from 'react';
import { fetchProducts } from '@/lib/api/products';

export default function ProductsPage() {
  const [products, setProducts] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  const loadProducts = async () => {
    try {
      setLoading(true);
      setError(null);
      
      const productsData = await fetchProducts();
      setProducts(productsData || []);
    } catch (err) {
      console.error('Load error:', err);
      setError(err.message || 'Failed to load products');
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    loadProducts();
  }, []);

  if (loading) {
    return (
      <div className="flex justify-center items-center h-64">
        <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-blue-500"></div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="p-4 bg-red-100 border-l-4 border-red-500 text-red-700 rounded">
        <p>Error: {error}</p>
        <button
          onClick={loadProducts}
          className="mt-2 px-4 py-2 bg-red-600 text-white rounded hover:bg-red-700 transition-colors"
        >
          Retry
        </button>
      </div>
    );
  }

  return (
    <div className="p-4">
      <h1 className="text-2xl font-bold mb-4">Products ({products.length})</h1>
      
      {products.length === 0 ? (
        <div className="text-center py-8">
          <p className="text-gray-500">No products found</p>
          <button
            onClick={loadProducts}
            className="mt-4 px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700 transition-colors"
          >
            Refresh
          </button>
        </div>
      ) : (
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
          {products.map(product => (
            <div key={product._id} className="border p-4 rounded-lg hover:shadow-md transition-shadow">
              <img
                src={product.imageUrl || '/placeholder-product.jpg'}
                alt={product.name}
                className="w-full h-48 object-cover mb-2 rounded"
                onError={(e) => e.target.src = '/placeholder-product.jpg'}
              />
              <h2 className="font-bold text-lg">{product.name}</h2>
              <p className="text-gray-600">${product.price?.toFixed(2)}</p>
              <p className={`text-sm ${
                product.stock > 0 ? 'text-green-600' : 'text-red-600'
              }`}>
                {product.stock} in stock
              </p>
            </div>
          ))}
        </div>
      )}
    </div>
  );
}